import * as tf from '@tensorflow/tfjs';

export class Model {
  constructor() {
    this.machine = null;
  }
  
  createAndCompileModel() {
    //instantiate our Sequential model with tf.sequential: 
    this.machine = tf.sequential();

    /* 
     * Add a two-dimensional convolutional layer.
     * For more information about convolutions see,
     * http://colah.github.io/posts/2014-07-Understanding-Convolutions/
    */
    this.machine.add(tf.layers.conv2d({
      inputShape: [300, 300, 1],
      kernelSize: 5,
      filters: 8,
      strides: 1,
      activation: 'relu',
      kernelInitializer: 'varianceScaling'
    }));

    //Add a max pooling layer
    this.machine.add(tf.layers.maxPooling2d({poolSize: [2, 2], strides: [2, 2]}));

    this.machine.add(tf.layers.conv2d({
      kernelSize: 5,
      filters: 16,
      strides: 1,
      activation: 'relu',
      kernelInitializer: 'varianceScaling'
    }));
    this.machine.add(tf.layers.maxPooling2d({poolSize: [2, 2], strides: [2, 2]}));
    this.machine.add(tf.layers.flatten());
    this.machine.add(tf.layers.dense({units: 10,
                               kernelInitializer: 'varianceScaling',
                               activation: 'softmax'}));

    var LEARNING_RATE = 0.15;
    var optimizer = tf.train.sgd(LEARNING_RATE);
    this.machine.compile({
      optimizer: optimizer,
      loss: 'categoricalCrossentropy',
      metrics: ['accuracy'],
    });
    console.log('Model created succesfully');
  }

  runAndValidateModel(data) {
    if(this.machine === null){
      console.log('Please create machine');
    }
    else if(data === null) {
      console.log('Please init input data');
    }
    else{
      //TBD - do something here
      //data.nextBatch();
    }
  }
  
}


