import React from 'react';

class ModelSettings extends React.Component {
  constructor () {
    super()
    this.state = {
      nrOfLayers: 0
    }
  }

  render() {
    var smallFont = {
      fontSize: 12
    };
    return(
      <div className="controlPanelLanel">
        <h3>General Model settings</h3>
        <hr />
        <p style={smallFont}>Learning rate<br/>
                             Sequential model (decided by me)<br/>
                             Optimiser<br/>
                             Loss metrics
        </p>
      </div>
    );
  }
}
 
export default ModelSettings;


