import React from 'react';

class NnSettings extends React.Component {
  constructor () {
    super()
    this.state = {
      nrOfLayers: 0
    }
  }

  render() {
    var smallFont = {
      fontSize: 12
    };
    return(
      <div className="controlPanelLanel">
        <h3>Neural layer(s)</h3>
        <hr />
        <p style={smallFont}>Due to lack of time the machine will have a fixed layer-configurarion. The machine will be configured with a flatten layer (to flatten the output from the convolutional layers) and a dense (fully connected layer) layer. The dense layer will have below configuration:</p>
        <ul>
          <li>output/neurons: set to nr. of categories</li>
          <li>activation func: softmax</li>
          <li>kernelInitializer: varianceScaling</li>
        </ul>
      </div>
    );
  }
}
 
export default NnSettings;


