import React from 'react';
import MySaveButton from './MySaveButton';

class ConvSettings extends React.Component {
  constructor () {
    super()
    this.state = {
      nrOfLayers: 2,
      activeLayer: 0,
      filterSize: ["5", "5"],
      stride: [],
      padding: [],
      activationFunction: [],
      poolLayer: []
    }
  }
  
  handleFilterSizeChange(event) {
    //this.setState({title: event.target.value})
    console.log(event.target.value);
    var filterSizeArray = this.state.filterSize;
    filterSizeArray[this.state.activeLayer] = event.target.value;
    this.setState({filterSize: filterSizeArray}, function () {
      console.log(this.state.filterSize[this.state.activeLayer]);
      console.log(this.state.filterSize);
    });
  }
  
  feedBackSettings() {
    console.log('Feedback settings');
  }

  render() {
    var smallFont = {
      fontSize: 12
    };
    return(
      <div className="controlPanelLanel">
        <h3>Convolutional layer(s)</h3>
        <hr />
        <p style={smallFont}>Due to lack of time the machine will have a fixed layer-configurarion. The machine will be configured with two 2D convolutional layers. Each layer is configured according to below:</p>
        <input type="number" name="filterSize" value={this.state.filterSize[this.state.activeLayer]} onChange={this.handleFilterSizeChange.bind(this)}/>
        <ul>
          <li>kernelsize: 5</li>
          <li>nr of filters: 8</li>
          <li>strides: 1</li>
          <li>activation func: relu</li>
          <li>kernelInitializer: varianceScaling</li>
          <li>max pooling layer:</li>
          <ul>
            <li>poolsize: 2x2</li>
            <li>stides: 2x2</li>
          </ul>
        </ul>
        <MySaveButton buttonBackground={this.state.saveButtonBackgroundColor}
                      buttonText='TBD'
                      onClick={this.feedBackSettings.bind(this)}/>
      </div>
    );
  }
}
 
export default ConvSettings;


