import React from 'react';

class ModelCtrl extends React.Component {
  constructor () {
    super()
    this.state = {
      modelOk: false
    }

    this.createMachine = this.createMachine.bind(this);
    this.runMachine = this.runMachine.bind(this);
  }
  
  createMachine() {
    this.props.createModel();
  }
  
  runMachine() {
    this.props.runModel();
  }

  render() {
    var boldFont = {
      fontWeight: 'bold'
    };
    return(
      <div className="controlPanelLanel">
        <h3>Create and Run</h3>
        <hr />
        <p style={boldFont}>Text</p>
        <button onClick={this.createMachine.bind(this)} >
          Create Machine
        </button>
        <button onClick={this.runMachine.bind(this)} >
          Run Machine
        </button>
      </div>
    );
  }
}
 
export default ModelCtrl;


