import React, { Component } from 'react';
import './App.css';
import Gallery from './Gallery';
import InputSetting from './InputSetting';
import ModelSettings from './ModelSettings';
import ConvSettings from './ConvSettings';
import NnSettings from './NnSettings';
import ModelCtrl from './ModelCtrl';
import Console from './Console';
import {getImageUrls_color, getImageUrls_gray, getImageUrls_binary} from './ImageUrls';
import {Model} from './Model';
import {ImageData} from './Data';

class App extends Component {
  constructor(props, context) {//Class constructor
    super(props, context);
    
    //Class state variables which will trigger rerendering when changed
    this.state = {
      galleryUrls: [],
      galleryLabels: [],
      galleryFormat: 'gray',
      galleryIsHidden: true,
      modelData: null,
      model: null,
      console: []
    }
    this.toggleGallery = this.toggleGallery.bind(this);
    this.saveFeedbackFromInputCtrl = this.saveFeedbackFromInputCtrl.bind(this);
    this.createModel = this.createModel.bind(this);
    this.runModel = this.runModel.bind(this);
    this.logToConsole = this.logToConsole.bind(this);
  }
  
  componentDidMount() {
    //Init data so that one can run the machine with default settings
    var initCategories = new Set();
    initCategories.add('crayfish');
    initCategories.add('elephant');
    initCategories.add('flamingo');
    initCategories.add('hedgehog');
    initCategories.add('kangaroo');
    initCategories.add('leopards');
    this.saveFeedbackFromInputCtrl('gray', initCategories, 85, 1);
  }
  
  logToConsole(string) {
    var currentConsole = this.state.console;
    currentConsole.push(string);
    this.setState({console: currentConsole});
  }
  
  saveFeedbackFromInputCtrl(format, categories, ratio, batchSize) {
    var consoleFeedback = 'Please add at least two categories';
    if(categories.size > 1) {
      this.setState({modelData: new ImageData()}, function () {
        this.state.modelData.load(format, categories, ratio, batchSize);
        consoleFeedback = 'Input data configurarion saved';
        this.logToConsole(consoleFeedback);
      });
    }
    else {
      this.logToConsole(consoleFeedback);
    }
  }
  
  runModel() {
    if(this.state.model !== null){
      this.state.model.runAndValidateModel(this.state.modelData);
    }
  }

  createModel() {
    this.setState({model: new Model()}, function () {
      this.state.model.createAndCompileModel();
    });
  }

  toggleGallery(label, format) {
    if((this.state.galleryLabels === label) && (this.state.galleryFormat === format)){
      this.setState({
        galleryIsHidden: true
      });
    }
    else {
      var urls = [];
      switch(format) {
        case 'color':
          urls = getImageUrls_color(label);
          break;
        case 'gray':
          urls = getImageUrls_gray(label);
          break;
        case 'binary':
          urls = getImageUrls_binary(label);
          break;
        default:
          urls = getImageUrls_color(label);
      }
      
      this.setState({
        galleryIsHidden: false
      });
      
      this.setState({
        galleryFormat: format
      });
      
      this.setState({galleryUrls: urls}, function () {
        //console.log();
      });
      this.setState({galleryLabels: label}, function () {
        //console.log();
      });
    }
  }

  render() {
    return (
      <div className="App">
        <header className="App-header">
          <h1 className="App-title">My image object recognition machine</h1>
        </header>
        <p className="App-intro">
          To get started, configure the network below and press the play button when ready.
        </p>
        <div className="controlPanel">
          <InputSetting toggleGallery={this.toggleGallery}
                        saveSettings={this.saveFeedbackFromInputCtrl}/>
          <ModelSettings />
          <ConvSettings/>
          <NnSettings />
          <ModelCtrl createModel={this.createModel} runModel={this.runModel}/>
          <Console textToAdd={this.state.console}/>
        </div>
        {!this.state.galleryIsHidden && <Gallery imageUrls={this.state.galleryUrls}
                                                 imageLabels={this.state.galleryLabels}/>}
      </div>
    );
  }
}

export default App;



